import React, { useState, useEffect } from "react";
import { WebView } from "react-native-webview";
import {
  useWindowDimensions,
  StyleSheet,
  Platform,
  View,
  Alert,
  Image,
  ImageBackground,
  Text,
} from "react-native";
import database from "../Firebase/config";
import { getDatabase, ref, set, push, update, child } from "firebase/database";
import AsyncStorage from "@react-native-async-storage/async-storage";

const date = new Date();
const timestamp = date.toString();

export default function PaymentScreen({ route, navigation }) {
  const { url, order_id, paymentData } = route.params;
  const url2 = `https://www.divyaayodhya.com/book_parking/payment/checkpayment/${order_id}`;
  const { height, width } = useWindowDimensions();

  const newPostKey = push(
    child(
      ref(database),
      `/Parking/parkingEntity/${paymentData.parkingId}/bokings`
    )
  ).key;

  function writeRef(parkingRef) {
    const newRefKey = push(child(ref(database), `/Parking/bookingsPanel`)).key;
    const updates = {};
    updates[`Parking/bookingsPanel/${newRefKey}`] = parkingRef;
    return update(ref(database), updates);
  }

  function writeData() {
    const data = {
      username: paymentData.name,
      phoneNo: paymentData.phoneNo,
      vehicleNo: paymentData.vehicleNo,
      duration: paymentData.duration,
      parkingName: paymentData.parkingName,
      address: paymentData.address,
      amount: paymentData.amount,
      verified: false,
      timestamp: timestamp,
      entryTime: "",
      exitTime: "",
      ref_id: newPostKey,
      parkingId: paymentData.parkingId,
    };

    const updates = {};
    updates[
      `/Parking/parkingEntity/${paymentData.parkingId}/bookings/` + newPostKey
    ] = data;

    const refData = {
      ref_id: newPostKey,
      parkingId: paymentData.parkingId,
    };
    writeRef(refData);

    return update(ref(database), updates);
  }

  const storeData = async (value) => {
    try {
      const jsonValue = JSON.stringify(value);
      await AsyncStorage.setItem("tickets", jsonValue);
    } catch (e) {}
  };

  const ticketRegistration = () => {
    writeData();
    const ticketData = {
      qrID: newPostKey,
      name: paymentData.name,
      phoneNo: paymentData.phoneNo,
      vehicleNo: paymentData.vehicleNo,
      duration: paymentData.duration,
      parkingName: paymentData.parkingName,
      address: paymentData.address,
      amount: paymentData.amount,
      verfied: false,
      timestamp: timestamp,
      parkingId: paymentData.parkingId,
    };

    storeData(ticketData);

    navigation.navigate("Tickets", { ticketData: ticketData });
  };

  // State to store payment status
  const [paymentStatus, setPaymentStatus] = useState(null);

  const handleWebViewNavigationStateChange = (newNavState) => {
    const { url } = newNavState;
    // console.log(newNavState);

    console.log("url1", url.length, url);
    console.log("url2 ", url2.length, url2);
    console.log(url == url2.trim());
    if (url === url2) {
      console.log("checking payment status");
      fetch(url)
        .then((response) => response.json())
        .then((data) => {
          // Assuming data contains the payment status
          setPaymentStatus(data);
          console.log("fetching status data");
          console.log(data);
        })
        .catch((error) => {
          console.error("Error fetching payment status:", error);
          Alert.alert("Error", "Failed to fetch payment status");
        });
    }
  };

  useEffect(() => {}, []);

  if (paymentStatus !== null && paymentStatus) {
    if (paymentStatus !== null && paymentStatus.status == 0) {
      console.log("calling ticket registration");
      setTimeout(() => {
        ticketRegistration();
      }, 1000);
    }
    return (
      <View style={{ flex: 1 }}>
        {paymentStatus.status === 0 ? (
          <ImageBackground
            style={{
              flex: 1,
              backgroundColor: "#FDFAE7",
              justifyContent: "center",
              alignItems: "center",
            }}
            source={{
              uri: "https://raw.githubusercontent.com/Arabhya07092007/HolyAyodhya-assets/main/homebg.png",
            }}
          >
            <View
              style={{
                backgroundColor: "white",
                elevation: 2,
                borderRadius: 10,
                padding: 10,
                width: width * 0.8,
                height: width * 0.8,
                justifyContent: "center",
                alignItems: "center",
              }}
            >
              <Image
                source={{
                  uri: "https://upload.wikimedia.org/wikipedia/commons/thumb/7/73/Flat_tick_icon.svg/2048px-Flat_tick_icon.svg.png",
                }}
                style={{ width: 90, height: 90 }}
              />
              <Text
                style={{
                  fontSize: 25,
                  color: "green",
                  fontWeight: "600",
                  marginTop: 20,
                }}
              >
                Payment Successful
              </Text>
              <Text>Thankyou for using our service </Text>
            </View>
          </ImageBackground>
        ) : (
          <ImageBackground
            style={{
              flex: 1,
              backgroundColor: "#FDFAE7",
              justifyContent: "center",
              alignItems: "center",
            }}
            source={{
              uri: "https://raw.githubusercontent.com/Arabhya07092007/HolyAyodhya-assets/main/homebg.png",
            }}
          >
            <View
              style={{
                backgroundColor: "white",
                elevation: 2,
                borderRadius: 10,
                padding: 10,
                width: width * 0.8,
                height: width * 0.8,
                justifyContent: "center",
                alignItems: "center",
              }}
            >
              <Image
                source={{
                  uri: "https://upload.wikimedia.org/wikipedia/commons/thumb/8/8f/Flat_cross_icon.svg/1024px-Flat_cross_icon.svg.png",
                }}
                style={{ width: 90, height: 90 }}
              />
              <Text
                style={{
                  fontSize: 25,
                  color: "red",
                  fontWeight: "600",
                  marginTop: 20,
                }}
              >
                Payment Failed
              </Text>
              <Text>Please Try Again</Text>
            </View>
          </ImageBackground>
        )}
      </View>
    );
  } else {
    return (
      <WebView
        source={{ uri: url }}
        style={{
          backgroundColor: "white",
          marginHorizontal: -80,
        }}
        onNavigationStateChange={handleWebViewNavigationStateChange} // Attach the function directly to onNavigationStateChange
      />
    );
  }
}
