import React, { useState, useEffect } from "react";
import {
  View,
  Text,
  FlatList,
  Image,
  TouchableOpacity,
  StyleSheet,
  ImageBackground,
  StatusBar,
  SafeAreaView,
  Dimensions,
} from "react-native";
const winWidth = Dimensions.get("window").width;
const winHeight = Dimensions.get("window").height;
import database from "../Firebase/config";
import { getDatabase, ref, set, push, update, child } from "firebase/database";
import AsyncStorage from "@react-native-async-storage/async-storage";

const date = new Date();
const timestamp = date.toString();

export default function ReviewSum({ navigation, route }) {
  const { paymentData } = route.params;

  function generateOrderId() {
    let result = "";
    const characters =
      "ABCDEFGHIJKLMNOPQRSTUVWXYZabcdefghijklmnopqrstuvwxyz0123456789";
    const charactersLength = characters.length;
    for (let i = 0; i < 8; i++) {
      result += characters.charAt(Math.floor(Math.random() * charactersLength));
    }
    return result;
  }

  const sendData = async (name, mobile, amount, email, address) => {
    console.log(name, mobile, amount, email, address);
    const order_id = `${Date.now()}-${generateOrderId()}`;
    console.log("orderId", order_id);
    const body = `order_id=${order_id}&name=${name}&mobile=${mobile}&amount=${amount}&address=${address}&email=${email}`;
    console.log(body);
    try {
      console.log("made the request for payment");
      const url = "https://divyaayodhya.com/book_parking/paymentRequest";
      const options = {
        method: "POST",
        headers: {
          "Content-Type": "application/x-www-form-urlencoded",
          "Access-Control-Allow-Origin": "*",
        },
        body: body,
      };

      const response = await fetch(url, options);

      if (response.ok) {
        const responseData = await response.text();
        console.log(response);
        console.log(response.url);
        // console.log(responseData);
        navigation.navigate("PaymentScreen", {
          // htmlContent: `${responseData}`,
          url: response.url,
          order_id: order_id,
          paymentData: paymentData,
        });
      } else {
        Alert.alert("Error", "Failed to send data");
      }
    } catch (error) {
      console.error("Error:", error);
      Alert.alert("Error", "Failed to send data");
    }
  };

  return (
    <SafeAreaView style={styles.cont}>
      <StatusBar barStyle={"dark-content"} backgroundColor={"#FDFAE7"} />
      <View>
        <View
          style={{
            margin: 10,
            paddingHorizontal: 15,
            paddingTop: 20,
            backgroundColor: "white",
            borderRadius: 10,
            elevation: 1,
            shadowColor: "#000",
            shadowOffset: {
              width: 0,
              height: 2,
            },
            shadowOpacity: 0.1,
            shadowRadius: 4,
            elevation: 5,
            marginTop: 10,
          }}
        >
          <View style={styles.itemCont}>
            <Text style={styles.lft}>Name</Text>
            <Text style={styles.rht}>{paymentData.name}</Text>
          </View>

          <View style={styles.itemCont}>
            <Text style={styles.lft}>Phone No.</Text>
            <Text style={styles.rht}>{paymentData.phoneNo}</Text>
          </View>

          <View style={styles.itemCont}>
            <Text style={styles.lft}>Vehicle No</Text>
            <Text style={styles.rht}>{paymentData.vehicleNo}</Text>
          </View>

          <View style={styles.itemCont}>
            <Text style={styles.lft}>Duration</Text>
            <Text style={styles.rht}>{paymentData.duration} hours</Text>
          </View>

          <View style={styles.itemCont}>
            <Text style={styles.lft}>Parking Area</Text>
            <Text style={styles.rht}>{paymentData.parkingName}</Text>
          </View>

          <View style={styles.itemCont}>
            <Text style={styles.lft}>Address</Text>
            <Text style={styles.rht}>{paymentData.address}</Text>
          </View>
        </View>

        <View
          style={{
            margin: 10,
            paddingHorizontal: 10,
            paddingTop: 10,
            backgroundColor: "white",
            borderRadius: 10,
            elevation: 1,
            shadowColor: "#000",
            shadowOffset: {
              width: 0,
              height: 2,
            },
            shadowOpacity: 0.15,
            shadowRadius: 4,
            elevation: 2,
          }}
        >
          <View style={[styles.itemCont, { marginTop: 10 }]}>
            <Text style={styles.lft}>Amount</Text>
            <Text style={styles.rht}>Rs {paymentData.amount}</Text>
          </View>
        </View>
      </View>

      <TouchableOpacity
        onPress={() => {
          sendData(
            paymentData.name,
            paymentData.phoneNo,
            // paymentData.amount,
            "1",
            paymentData.email,
            paymentData.userAddress
          );
        }}
        style={{
          backgroundColor: "#F0A936",
          padding: 10,
          marginHorizontal: 10,
          borderRadius: 10,
          elevation: 2,
          marginBottom: 20,
        }}
      >
        <Text
          style={{
            color: "white",
            fontSize: 18,
            fontWeight: "700",
            letterSpacing: 0.17,
            textAlign: "center",
          }}
        >
          Confirm
        </Text>
      </TouchableOpacity>
    </SafeAreaView>
  );
}

const styles = StyleSheet.create({
  cont: {
    backgroundColor: "#FDFAE7",
    flex: 1,
    justifyContent: "space-between",
  },
  lft: {
    color: "#616161",
    fontWeight: "600",
    width: "50%",
  },
  rht: {
    justifyContent: "flex-end",
    color: "black",
    fontWeight: "600",
    width: "50%",
    // backgroundColor: 'red',
    // textAlign: 'right',
  },
  itemCont: {
    // backgroundColor:"red",
    flexDirection: "row",
    justifyContent: "space-between",
    marginBottom: 20,
  },
});
