import * as React from "react";
import {
  createStackNavigator,
  CardStyleInterpolators,
} from "@react-navigation/stack";
import ParkingFeed from "../Parking/ParkingFeed";
import ParkingComplex from "../Parking/ParkingComplex";
import BookingEntry from "../Parking/BookingEntry";
import ReviewSum from "../Parking/ReviewSum";
import Tickets from "../Parking/Tickets";
import ParkBottomNav from "./ParkBottomNav";
import HeaderBack from "../Components/HeaderBack";
import PaymentScreen from "../Parking/PaymentScreen";
import ComingSoon from "../Parking/ComingSoon";

const Stack = createStackNavigator();

function ParkStack() {
  return (
    <Stack.Navigator
      screenOptions={{
        cardStyleInterpolator: CardStyleInterpolators.forHorizontalIOS,
        headerShadowVisible: true,
        headerTintColor: "black",
        headerStyle: { backgroundColor: "#FDFAE7" },
        headerShown: true,
        headerTitleAlign: "center",
        headerTitleStyle: { fontSize: 20, fontWeight: "600" },
      }}
      // initialRouteName="PaymentScreen"
      // initialRouteName="ParkBottomNav"
    >
      <Stack.Screen
        name="ParkBottomNav"
        component={ParkBottomNav}
        options={{
          headerShown: false,
        }}
      />
      <Stack.Screen
        name="ParkingComplex"
        component={ParkingComplex}
        options={{ headerShown: false }}
      />
      <Stack.Screen
        name="BookingEntry"
        component={BookingEntry}
        options={({ route }) => ({ title: route.params.name })}
      />
      <Stack.Screen
        name="ReviewSum"
        component={ReviewSum}
        options={{
          title: "Review Summary",
        }}
      />
      <Stack.Screen
        name="Tickets"
        component={Tickets}
        options={{
          title: "Tickets",
          headerLeft: () => <HeaderBack />,
        }}
      />
      <Stack.Screen
        name="PaymentScreen"
        component={PaymentScreen}
        options={{
          title: "Payment",
        }}
      />
      <Stack.Screen
        name="ComingSoon"
        component={ComingSoon}
        options={{ headerShown: false }}
      />
    </Stack.Navigator>
  );
}

export default ParkStack;
